html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea,
button {
  -webkit-appearance: none;
}

input[type="submit"],
button,
a {
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner,
a::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol, li {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
  -webkit-tap-highlight-color: transparent;
}

[hidden] {
  display: none !important;
}

input[type="button"],
input[type="submit"],
input[type="reset"],
input[type="file"]::-webkit-file-upload-button,
button {
  border-radius: 0;
}

.ui-wrapper {
  position: relative;
  padding-left: 200px;
  height: 100%;
}

.ui-menu {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  overflow: auto;
  width: 200px;
  color: #fff;
  background: #2e99db;
  font-size: 14px;
  text-transform: uppercase;
}

.ui-menu__item {
  padding: 15px 10px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  cursor: pointer;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.ui-menu__item.is-active {
  background: rgba(0, 0, 0, 0.1);
}

.ui-content {
  display: none;
  overflow: auto;
  height: 100%;
}

.ui-content.is-open {
  display: block;
}

.ui-container {
  padding: 10px 5px;
  font-size: 0;
}

.ui-container:not(:first-child) {
  border-top: 10px solid #333;
}

.ui-section {
  margin: 10px 0;
  border-top: 2px solid #333;
}

.ui-col {
  display: inline-block;
  vertical-align: bottom;
  padding: 10px;
  margin-bottom: 2%;
  border: 2px solid #eee;
  width: 100%;
  background: #fff;
  font-size: 14px;
}

.ui-col_md {
  width: 49%;
}

.ui-col_md:nth-child(even) {
  margin-right: 1%;
}

.ui-col_md:nth-child(odd) {
  margin-left: 1%;
}

.ui-col_sm {
  width: 32.33%;
  margin-right: 1%;
}

.ui-component {
  padding-top: 20px;
  border-top: 1px solid #eee;
}

.ui-description {
  padding-top: 10px;
  padding-bottom: 20px;
  font-family: 'Roboto Regular', Arial, sans-serif;
  font-size: 16px;
}

.ui-description + .ui-code {
  padding-top: 20px;
  border-top: 1px solid #eee;
}

.ui-code {
  padding-top: 10px;
  padding-bottom: 20px;
}

.ui-label {
  padding-bottom: 5px;
  font-family: 'Roboto Bold', Arial, sans-serif;
  font-size: 14px;
  text-transform: uppercase;
}

.ui-title {
  font-family: 'Roboto Bold', Arial, sans-serif;
  font-size: 28px;
  padding: 10px;
}

.ui-subtitle {
  font-family: 'Roboto Medium', Arial, sans-serif;
  font-size: 24px;
  padding: 10px;
}

.ui-list {
  font-size: 14px;
}

.ui-list li {
  padding: 3px 10px;
}

.ui-pages {
  position: relative;
  padding-top: 80px;
  height: 100%;
}

.ui-pages__header {
  position: absolute;
  top: 10px;
  left: 5px;
}

.ui-pages__title {
  float: left;
  padding-right: 20px;
}

.ui-pages__select {
  float: left;
  width: 200px;
}

.ui-pages__iframe {
  border: 2px solid #333;
  width: 100%;
  height: 100%;
}

.ui-pages__iframe iframe {
  width: 100%;
  height: 100%;
}
